import { motion, useMotionValue, useTransform, useScroll } from "framer-motion";
import { useEffect, useState } from "react";

export default function Home() {
  const mouseX = useMotionValue(0);
  const mouseY = useMotionValue(0);

  const moveJX = useTransform(mouseX, [0, window.innerWidth], [30, -30]);
  const moveYX = useTransform(mouseX, [0, window.innerWidth], [-20, 20]);
  const movePhotoX = useTransform(mouseX, [0, window.innerWidth], [20, -20]);

  useEffect(() => {
    const handleMouseMove = (e) => {
      mouseX.set(e.clientX);
      mouseY.set(e.clientY);
    };
    window.addEventListener("mousemove", handleMouseMove);
    return () => window.removeEventListener("mousemove", handleMouseMove);
  }, [mouseX, mouseY]);

  const { scrollYProgress } = useScroll();
  const opacity = useTransform(scrollYProgress, [0, 0.3], [1, 0]);

  // ✅ 부드러운 타이핑 효과
  const fullText = "창의적으로 생각하고, 사용자의 경험으로 완성하는 UX UI 디자이너";
  const [displayText, setDisplayText] = useState("");
  const [showCursor, setShowCursor] = useState(true);

  useEffect(() => {
  let i = 0;
  const interval = setInterval(() => {
    setDisplayText(fullText.slice(0, i));
    i++;
    if (i > fullText.length) {
      clearInterval(interval);
      setShowCursor(false); // 👉 타이핑 끝나면 즉시 커서 숨기기
    }
  }, 80);
  return () => clearInterval(interval);
}, []);


  return (
    <motion.section id="home" className="hero container" style={{ opacity }}>
      <div className="hero-wrapper">
        <p className="hero-text">
          {displayText.split("UX UI 디자이너")[0]}
          {displayText.includes("UX UI 디자이너") && (
            <span className="highlight">UX UI 디자이너</span>
          )}
          {showCursor && <span className="cursor">|</span>}
        </p>

        <div className="hero-main">
          <motion.img
            src={process.env.PUBLIC_URL + "/imgs/homeJ.png"}
            alt="J"
            className="hero-j"
            style={{ x: moveJX, y: moveYX, zIndex: 5 }}
            initial={{ opacity: 0, scale: 0.5, y: 50 }}
            animate={{ opacity: 1, scale: [1.2, 1], y: 0 }}
            transition={{ delay: 0.2, duration: 1.2, ease: "easeOut" }}
          />

          <motion.div
            className="hero-photo"
            style={{ x: movePhotoX, zIndex: 3 }}
            initial={{ opacity: 0, scale: 0.5, rotate: -8 }}
            animate={{ opacity: 1, scale: [1.1, 1], rotate: -2 }}
            transition={{ delay: 0.8, duration: 1.2, ease: "easeOut" }}
          >
            <img
              src={process.env.PUBLIC_URL + "/imgs/homeMain.png"}
              alt="폴라로이드 배경"
              className="profile-bg"
            />
            <img
              src={process.env.PUBLIC_URL + "/imgs/mainphoto.png"}
              alt="박지영 프로필사진"
              className="jyphoto"
            />
            <p className="name">박지영</p>
          </motion.div>

          <motion.img
            src={process.env.PUBLIC_URL + "/imgs/homeY.png"}
            alt="Y"
            className="hero-y"
            style={{ x: moveYX, zIndex: 2 }}
            initial={{ opacity: 0, scale: 0.5, y: 50 }}
            animate={{ opacity: 1, scale: [1.3, 1], y: 0 }}
            transition={{ delay: 1.4, duration: 1.2, ease: "easeOut" }}
          />
        </div>
      </div>
    </motion.section>
  );
}
