import { useState } from "react";

function DeviceDisplay({ work }) {
  const { id, img, site, screenImg, title } = work;

  // ✅ Next.js 프로젝트 (아이패드+노트북+아이폰)
  if (id === 5) {
    // 각 기기별 개별 화면 이미지 지정
    const deviceScreens = {
      ipad: process.env.PUBLIC_URL + "/imgs/littlefarmer-ipad.png",
      laptop: process.env.PUBLIC_URL + "/imgs/littlefarmer.png",
      iphone: process.env.PUBLIC_URL + "/imgs/littlefarmer-iphone.png",
    };

    return (
      <div className="multi-device">
        {["ipad", "laptop", "iphone"].map((device) => (
          <div key={device} className={`${device}-frame`}>
            {/* 각 기기 프레임 이미지 */}
            <img
              src={process.env.PUBLIC_URL + `/imgs/${device}.png`}
              alt={device}
              className={device}
            />
            {/* 각 기기별 화면 이미지 다르게 지정 */}
            <div className={`${device}-screen`}>
              <a href={site} target="_blank" rel="noopener noreferrer">
                <img
                  src={deviceScreens[device]}
                  alt={`${title} ${device} 화면`}
                  className="screen-content"
                />
              </a>
            </div>
          </div>
        ))}
      </div>
    );
  }

  // 아이폰 단독 (모바일 전용)
if (img.includes("iphone")) {
  // Series Up (Vue 프로젝트)만 모바일 창으로 띄우기
  const handleMobileOpen = (e) => {
    e.preventDefault();
    if (id === 3) {
      window.open(
        site,
        "_blank",
        "width=430,height=932,noopener,noreferrer" // ✅ 모바일 규격
      );
    } else {
      window.open(site, "_blank"); // ✅ 나머지는 일반 새 창
    }
  };

  return (
    <div className="iphone-frame">
      <img src={img} alt="아이폰" className="iphone" />
      <div className="iphone-screen">
        <a href={site} target="_blank" rel="noopener noreferrer" onClick={handleMobileOpen}>
          <img
            src={screenImg}
            alt={`${title} 화면`}
            className="screen-content"
          />
        </a>
      </div>
    </div>
  );
}


  // 기본 (노트북)
  return (
    <div className="laptop-frame">
      <img src={img} alt="노트북" className="laptop" />
      <div className="laptop-screen">
        <a href={site} target="_blank" rel="noopener noreferrer">
          <img
            src={screenImg}
            alt={`${title} 화면`}
            className="screen-content"
          />
        </a>
      </div>
    </div>
  );
}


